@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
    background-color: v.$primario;

    .contenido-footer {
        padding: 5rem;

        @include m.tablet {
            @include m.grid(3, 2rem);
        }
    }

    .copyright {
        text-align: center;
        margin: 0rem;
        padding: 2rem;
        color: v.$blanco;
    }
}

.footer-box {
    font-family: v.$fuenteCursiva;
    text-align: center;
    margin-bottom: v.$separacion;

    @include m.tablet {
        margin-bottom: 0rem;
    }

    h3{
        font-family: v.$fuenteCursiva;
        margin: 0rem 0rem 1rem 0rem;
        font-size: 2.4rem;
        color: v.$secundario;
    }

    p, a {
        font-size: 2rem;
        margin: 0rem;
    }
}
